import { useEffect, useState } from "react";
import { isAndroid, isIOS } from "react-device-detect";
import { useDispatch, useSelector } from "react-redux";
import { metamaskAppUrl } from "../contracts";
import walletStatus from '../store/walletStatus';

const useConnectWallet = () => {
  const dispatch = useDispatch();
  const [ isNotFound, setIsNotFound ] = useState(false);
  const { status, walletId } = useSelector(state => state.walletStatus);


  // conntected
  // dispatch(walletStatus.actions.setWalletdStatusConnected());
  

  // const [ status, setStatus ] = useState('disconnected');
  // const [ walletId, setWalletId ] = useState(null);

  function simplifyAddress(address) {
    return address.slice(0,6) + '...' + address.slice(-4);
  }

  const handleSetWalletId = (id) => {
    dispatch(walletStatus.actions.setWalletdStatusConnected());
    dispatch(walletStatus.actions.setWalletId(id));

    // setStatus('connected');
    // setWalletId(id);
  }

  const handleClearWalletId = () => {

    dispatch(walletStatus.actions.setWalletdStatusDisconnected());
    dispatch(walletStatus.actions.setWalletId(null));
    // setStatus('disconnected');
    // setWalletId(null);
  }

  useEffect(async () => {
    // await fetchWalletAPI();
  }, [])

  async function fetchWalletAPI() {
    // const address = await getWalletAddress();
    const address = await getEthereumAddress({ method: "eth_accounts" });

    // First get wallet address
    if (address === null) {
      setIsNotFound(true);
    }
    if (address && address.length > 0){
      handleSetWalletId(simplifyAddress(address));
    }
    addWalletListener();
  }
  
  function addWalletListener() {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          handleSetWalletId(simplifyAddress(accounts[0]))
        } else {
          handleClearWalletId();
        }
      });
    } else {
      handleClearWalletId();
      setIsNotFound(true);
    }
  }

  async function handleConnectWallet () {
    const isMobileDevice = isAndroid || isIOS;
    const isAppNotFound = !window.ethereum || !window.ethereum.isMetaMask;
    if (isMobileDevice && isAppNotFound) {
      window.location = metamaskAppUrl;
    }
    else {
      const address = await getEthereumAddress({ method: "eth_requestAccounts" });
      if (address === null) {
        setIsNotFound(true);
      }
      if (address && address.length > 0){
        handleSetWalletId(simplifyAddress(address));
      }
    }
  }

  const getEthereumAddress = async (request) => {
    let address = null;
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request(request);
        address = addressArray[0];
      }
      catch (err) {
        address = '';
        console.log(err);
      }
    } else { 
      address = null;
    }
    return address;
  };

  return { 
    walletId,
    status,
    isNotFound,
    onConnect: handleConnectWallet
  }
  
}

export default useConnectWallet;