import React from 'react';
import styled from 'styled-components';
import { respondTo } from '../../utils/responsive';
import { _w } from '../../utils/wordingSystem';

const HeadingSection = () => {
  const wording = _w('homepage.heading');
  
  return (
    <Root>
      <Heading>
        <img src={wording.banner} alt="" />
      </Heading>
    </Root>
  )
}


const Root = styled.div`
  position: relative;
  ${respondTo.md} {
    width: 100vw;
    overflow: hidden;
  }
`
const Heading = styled.div`
  position: relative;
  box-sizing: border-box;
  ${respondTo.md} {
    height: 100vh;
  }
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 15vh;
    background-image: linear-gradient(180deg, hsla(0, 0%, 100%, .5) 0%, hsla(0, 0%, 100%, 0) 80%, hsla(0, 0%, 100%, 0) 100%);
  }
  &:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 15vh;
    background-image: linear-gradient(0deg, hsla(0, 0%, 100%, .75) 0%, hsla(0, 0%, 100%, 0) 80%, hsla(0, 0%, 100%, 0) 100%);
  }
  img {
    display: block;
    width: 100%;
    height: auto;
    ${respondTo.md} {
      object-fit: cover;
      height: 100%;
    }
  }
`


export default HeadingSection;