import React, { useRef } from 'react';
import Slick from 'react-slick';
import styled, { css } from 'styled-components';
import { ArrowPrev, ArrowNext } from '../Icons';
import { colors } from '../../constants/colors';
import { respondTo } from '../../utils/responsive';

const Carousel = ({ className='', controller=true, children, setting={} }) => {
  const carouselRef = useRef(null);
  const slickSettings = {
    arrows: false,
    infinite: true,
    dots: true,
    slidesToShow: 1,
    ...setting
  };

  function handleClickArrowButton(e) {
    if (carouselRef.current) {
      const { direction } = e.currentTarget.dataset;
      if (direction === 'prev') carouselRef.current.slickPrev();
      if (direction === 'next') carouselRef.current.slickNext();
    }
  }


  return (
    <Root qty={children.length}>
      { controller &&
        <NavContainer>
          <button data-direction="prev" onClick={handleClickArrowButton}>
            <ArrowPrev />
          </button>
          <button data-direction="next" onClick={handleClickArrowButton}>
            <ArrowNext />
          </button>
        </NavContainer>
      }
      <CarouselBody>
        <Slick className={className} ref={carouselRef} {...slickSettings}>
          { children.map((child, i) => <div key={i}>{ child }</div>) }
        </Slick>
      </CarouselBody>
    </Root>
  )
}

const Root = styled.div`
  position: relative;
  .slick-dots {
    z-index: 1;
    li {
      margin: 0 !important;
      padding: 0 !important;
      width: auto;
      height: auto;
    }
    button {
      width: auto;
      margin: 0 !important;
      padding: 0 4px;
      height: auto;
      box-sizing: border-box;
      ${respondTo.md} {
        margin: 0 3px;
      }
    }
    button:before {
      position: relative;
      display: inline-block;
      border-radius: 8px;
      margin: 0;
      width: 30px;
      ${({ qty }) => css`
        max-width: ${90 / qty}vw;
      `}
      height: 4px;
      color: transparent;
      background: ${colors.blue};
      opacity: .15;
      transition: all .3s ease;
    }
    .slick-active button:before {
      color: transparent;
      opacity: 1;
    }
  }
`

const CarouselBody = styled.div`
  position: relative;
  width: 100%;
  box-sizing: border-box;
`

const NavContainer = styled.div`
  > button {
    position: absolute;
    top: 0;
    width: 5%;
    height: 100%;
    border: 0;
    background: transparent;
    z-index: 2;
    color: ${colors.blue};
    &:first-child {
      left: -20px;
    }
    &:last-child {
      right: -20px;
    }
  }
`
export default Carousel;
