import React, { useState } from 'react';
import styled from 'styled-components';
import { colors } from '../../constants/colors';
import { respondTo } from '../../utils/responsive';
import useConnectWallet from '../../utils/useConnectWallet';
import { _w } from '../../utils/wordingSystem';

const MintPage = () => {
  const wording = _w('setting.mint');
  const [ currentMint, setCurrentMint ] = useState(wording.max);
  const [ currentStatus, setCurrentStatus ] = useState('welcome');
  const { status, walletId, onConnect, isNotFound } = useConnectWallet();

  console.log({status, walletId, isNotFound})

  function handleClickNumber(e) {
    const number = Number(e.currentTarget.dataset.number);
    setCurrentMint(number);
  }

  function renderMintSelector() {
    const selectors = [];
    for (let i = 1; i <= wording.max; i++) {
      selectors.push(
        <QtyButton
          key={'mint_selector_' + i}
          className={currentMint === i ? 'active' : ''}
          data-number={i}
          onClick={handleClickNumber}>{i}</QtyButton>
      );
    }
    return selectors;
  }

  function handleChangeStatus(e) {
    const status = e.currentTarget.value;
    setCurrentStatus(status);
  }
  
  return (
    <Root>
      <Background>
        <img className="desktop" src="/images/mint-bg.png" alt="" />
        <img className="mobile" src="/images/mint-bg-m.png" alt="" />
      </Background>
      <Main>
        <img src="/images/mint-main.png" alt="" />
        <Monitor
          data={wording[currentStatus]}
          mintMax={wording.max}
          mintPrice={wording.price}
          currentMint={currentMint}
          clickNumber={handleClickNumber} />
        <Ticket><img src="/images/mint-ticket.png" alt="" /></Ticket>
        <ButtonBar>
          <Button>OK</Button>
        </ButtonBar>
      </Main>
      <Temp>
        <span>status:</span>
        <select name="" id="" onChange={handleChangeStatus}>
          <option value="welcome">welcome</option>
          <option value="normal">normal</option>
          <option value="error">error</option>
          <option value="unconnected">unconnected</option>
          <option value="unmatched">unmatched</option>
          <option value="select">select</option>
          <option value="qty">qty</option>
          <option value="success">success</option>
          <option value="failed">failed</option>
        </select>
      </Temp>
      <MobileButtonBar>
        <div className="qty">
          { renderMintSelector() }
        </div>
        <Button>OK</Button>
      </MobileButtonBar>
    </Root>
  )
}

const Monitor = ({ data, currentMint, mintMax, mintPrice, clickNumber }) => {
  const { type } = data;

  function renderMintSelector() {
    const selectors = [];
    for (let i = 1; i <= mintMax; i++) {
      selectors.push(
        <button
          key={'monitor_selector_' + i}
          className={currentMint === i ? 'active' : ''}
          data-number={i}
          onClick={clickNumber}>{i}</button>
      );
    }
    return selectors;
  }

  function applyVarious({source, replace}) {
    let result = source;
    Object.keys(replace).forEach(key => {
      const formatterValue = `<span>${replace[key]}</span>`
      result = result.replace(key, formatterValue);
    });
    return result;
  }
  
  return (
    <MonitorRoot>
      { type === 'heading' &&
        <div className="heading">{ data.content }</div>
      }
      { (type === 'text' || type === 'mint') &&
        <div className="text" dangerouslySetInnerHTML={{__html: data.content}} />
      }
      {
        type === 'img' &&
        <img src={data.content} />
      }
      {
        type === 'status' &&
        <>
          <div className="title">{ data.title }</div>
          <div className="content">{ data.content }</div>
        </>
      }
      {
        type === 'various' &&
        <div className="text"
          dangerouslySetInnerHTML={{
            __html: applyVarious({
              source: data.content,
              replace: {
                '$qty': currentMint,
                '$price': Math.floor(currentMint * mintPrice * 100) / 100
              }
            })
          }} />
      }
      { type === 'mint' &&
        <div className="buttonbar">
          { renderMintSelector() }
        </div>
      }
    </MonitorRoot>
  )
}

